/**
  antd
 */
.ant-spin-container {
  max-height: 100vh!important;
  min-height: 75vh!important;
}
.ant-spin-nested-loading > div > .ant-spin {
  top: 40vh!important;
}
.uiTable :global .ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td {
  background: #ecf3f8;
}
.uiTable :global .ant-table-content {
  width: 100%;
}
.uiTable :global .anticon.anticon-caret-down.ant-table-column-sorter-down.on {
  color: #1bc0cb;
}
.uiTable :global .ant-table-thead > tr > th.ant-table-column-sort {
  background-color: #e5f6fc !important;
}
.uiTable :global .ant-table-thead > tr > th {
  background: #e5f6fc;
  color: #333;
  border-bottom: none;
}
.uiTable :global .ant-table-header::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #000000;
}
.uiTable :global .ant-table-header::-webkit-scrollbar {
  width: 0;
}
.uiTable :global .ant-table-header::-webkit-scrollbar-track {
  background: transparent;
}
.uiTable :global .ant-table-body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.35);
}
.uiTable :global .ant-table-body::-webkit-scrollbar-track {
  background: transparent;
}
.uiTable :global .ant-table-row > td {
  padding: 8px 24px !important;
  height: 40px;
}
.uiTable :global .ant-table-tbody > tr > td {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: none;
}
.uiTable :global .ant-table-scroll table .ant-table-fixed-columns-in-body {
  visibility: visible;
}
.uiTable :global .ant-table-scroll table .ant-table-fixed-columns-in-body > * {
  visibility: hidden;
}
.uiTable :global .ant-table-thead > tr > th {
  white-space: nowrap;
  padding: 8px 24px;
  font-size: 14px;
  line-height: 1;
  height: 40px;
}
.uiTable :global .ant-table-thead > tr > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover {
  background: #ecf3f8;
}
.uiTable :global .ant-table-thead > tr > th.ant-table-column-sort {
  background: #ecf3f8;
}
.uiTable :global .ant-table-thead > tr > th .ant-table-column-sorter-down.on {
  color: #1bc0cb;
}
.uiTable :global .ant-table-fixed-right .ant-table-body-inner,
.uiTable :global .ant-table-fixed-left .ant-table-body-inner {
  overflow-y: auto !important;
}
/**
  
 */
.line-ellipsis-4 {
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  overflow: hidden;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
}
.line-ellipsis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



