div.wrap {
  width: 20px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  /**/
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  /**/
}
.wrap > div.cube {
  width: 20px;
  height: 20px;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: rotateX(-50deg) rotateY(-50deg) rotateZ(0deg);
          transform: rotateX(-50deg) rotateY(-50deg) rotateZ(0deg);
  /**/
  -webkit-animation: move 8s infinite linear;
          animation: move 8s infinite linear;
  /**/
}
/**/
@-webkit-keyframes move {
  0% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg);
  }
  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg);
  }
}
@keyframes move {
  0% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg);
  }
  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg);
  }
}
.cube > div {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  position: absolute;
  background: #000;
  -webkit-box-shadow: 0 0 10px currentColor;
          box-shadow: 0 0 10px currentColor;
  /*currentColor color */
  -webkit-transition: background 0.4s ease-in-out, -webkit-box-shadow 0.4s ease-in-out;
  transition: background 0.4s ease-in-out, -webkit-box-shadow 0.4s ease-in-out;
  -o-transition: background 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
  transition: background 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
  transition: background 0.4s ease-in-out, box-shadow 0.4s ease-in-out, -webkit-box-shadow 0.4s ease-in-out;
  /*   */
}
.cube:hover > div {
  background: currentColor;
  -webkit-box-shadow: 0 0 20px currentColor;
          box-shadow: 0 0 20px currentColor;
}
.cube div.out-front {
  color: deeppink;
  -webkit-transform: translateZ(10px);
          transform: translateZ(10px);
  /*  */
  /*background:currentColor;*/
}
.cube div.out-back {
  color: seagreen;
  -webkit-transform: translateZ(-10px) rotateY(180deg);
          transform: translateZ(-10px) rotateY(180deg);
  /*  */
  /*background:currentColor;*/
}
.cube div.out-left {
  color: skyblue;
  -webkit-transform: translateX(-10px) rotateY(-90deg);
          transform: translateX(-10px) rotateY(-90deg);
  /*background:currentColor;*/
}
.cube div.out-right {
  color: lightcoral;
  -webkit-transform: translateX(10px) rotateY(90deg);
          transform: translateX(10px) rotateY(90deg);
  /*background:currentColor;*/
}
.cube div.out-top {
  color: mediumseagreen;
  -webkit-transform: translateY(-10px) rotateX(90deg);
          transform: translateY(-10px) rotateX(90deg);
  /*background:currentColor;*/
}
.cube div.out-bottom {
  color: dodgerblue;
  -webkit-transform: translateY(10px) rotateX(-90deg);
          transform: translateY(10px) rotateX(-90deg);
  /*background:currentColor;*/
}



