.modal {
  height: 680px;
}
.modal :global .ant-modal-content {
  height: 100%;
}
.modal :global .ant-modal-body {
  height: 80%;
  padding: 12px 24px;
}
.modal :global .ant-table-body::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.35);
}
.modal :global .ant-table-body::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.modal :global .ant-table-body::-webkit-scrollbar-track {
  background: transparent;
}



